"use client";
import { useGlobalContext } from "@/context/globalContext";
import { LogIn, UserPlus, Menu, X } from "lucide-react";
import Image from "next/image";
import Link from "next/link";
import { usePathname } from "next/navigation";
import React, { useState } from "react";
import Profile from "./profile";



function Header() {
  const { isAuthenticated } = useGlobalContext();
  const pathname = usePathname();
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <header className="px-4 md:px-10 py-6 bg-[#D7DEDC] text-gray-500 ">
      <div className="flex justify-between items-center ">
        <Link href={"/"} className="flex items-center gap-2 " >
          <Image src="/appLogo.png" alt="logo" width={45} height={45} />
          <h1 className="font-extrabold text-2xl text-[#7263f3]">Prashikshan</h1>
        </Link>

        {/* Desktop Navigation */}
        <nav className="hidden md:flex items-center gap-8">
          <Link
            href={"/findwork"}
            className={`py-2 px-6 rounded-md ${
              pathname === "/findwork"
                ? "text-[#7263F3] border-[#7263F3] border bg-[#7263F3]/10"
                : ""
            }`}
          >
            Find Work
          </Link>
          <Link
            href={"/myjobs"}
            className={`py-2 px-6 rounded-md ${
              pathname === "/myjobs"
                ? "text-[#7263F3] border-[#7263F3] border bg-[#7263F3]/10"
                : ""
            }`}
          >
            My Jobs
          </Link>
          <Link
            href={"/post"}
            className={`py-2 px-6 rounded-md ${
              pathname === "/post"
                ? "text-[#7263F3] border-[#7263F3] border bg-[#7263F3]/10"
                : ""
            }`}
          >
            Post a Job
          </Link>
        </nav>

        {/* Mobile Menu Button */}
        <button
          className="md:hidden"
          onClick={() => setIsMenuOpen(!isMenuOpen)}
        >
          {isMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
        </button>

        {/* Auth Buttons */}
        <div className="hidden md:flex items-center gap-4">
          {isAuthenticated ? (
            <Profile />
          ) : (
            <div className="flex items-center gap-6">
              <Link
                href={"https://prashikshan.onrender.com/login"}
                className="py-2 px-6 rounded-md border flex items-center gap-4 bg-[#7263F3] text-white border-[#7263F3] hover:bg-[#7263F3]/90 transition-all duration-200 ease-in-out"
              >
                <LogIn className="w-4 h-4" />
                Login
              </Link>
              <Link
                href={"https://prashikshan.onrender.com/login"}
                className="py-2 px-6 rounded-md border flex items-center gap-4 border-[#7263F3] text-[#7263F3] hover:bg-[#7263F3]/10 transition-all duration-200 ease-in-out"
              >
                <UserPlus className="w-4 h-4" />
                Register
              </Link>
            </div>
          )}
        </div>
      </div>
      

      {/* Mobile Navigation */}
      {isMenuOpen && (
        <div className="md:hidden mt-4 pb-4 border-t border-gray-300">
          <nav className="flex flex-col gap-4 mt-4">
            <Link
              href={"/findwork"}
              className={`py-2 px-4 rounded-md ${
                pathname === "/findwork"
                  ? "text-[#7263F3] border-[#7263F3] border bg-[#7263F3]/10"
                  : ""
              }`}
              onClick={() => setIsMenuOpen(false)}
            >
              Find Work
            </Link>
            <Link
              href={"/myjobs"}
              className={`py-2 px-4 rounded-md ${
                pathname === "/myjobs"
                  ? "text-[#7263F3] border-[#7263F3] border bg-[#7263F3]/10"
                  : ""
              }`}
              onClick={() => setIsMenuOpen(false)}
            >
              My Jobs
            </Link>
            <Link
              href={"/post"}
              className={`py-2 px-4 rounded-md ${
                pathname === "/post"
                  ? "text-[#7263F3] border-[#7263F3] border bg-[#7263F3]/10"
                  : ""
              }`}
              onClick={() => setIsMenuOpen(false)}
            >
              Post a Job
            </Link>
          </nav>
          {!isAuthenticated && (
            <div className="flex flex-col gap-4 mt-4">
              <Link
                href={"https://prashikshan.onrender.com/login"}
                className="py-2 px-4 rounded-md border flex items-center justify-center gap-4 bg-[#7263F3] text-white border-[#7263F3] hover:bg-[#7263F3]/90 transition-all duration-200 ease-in-out"
                onClick={() => setIsMenuOpen(false)}
              >
                <LogIn className="w-4 h-4" />
                Login
              </Link>
              <Link
                href={"https://prashikshan.onrender.com/login"}
                className="py-2 px-4 rounded-md border flex items-center justify-center gap-4 border-[#7263F3] text-[#7263F3] hover:bg-[#7263F3]/10 transition-all duration-200 ease-in-out"
                onClick={() => setIsMenuOpen(false)}
              >
                <UserPlus className="w-4 h-4" />
                Register
              </Link>
            </div>
          )}
        </div>
      )}
    </header>
  );
}

export default Header
