"use client";
import React from "react";
import { useParams, useRouter } from "next/navigation";
import Image from "next/image";
import { toast } from "react-hot-toast";
import { useJobsContext } from "@/context/jobContext";
import { useGlobalContext } from "@/context/globalContext";
import { formatDates } from "@/utils/formatDate";
import Header from "@/component/Header";
import Footer from "@/component/Footer";
import JobCard from "@/component/jobItem/jobCard";
import { Job } from "@/types/custom";
import formatMoney from "@/utils/formatMoney";
import { Bookmark, ExternalLink } from "lucide-react"; // ✅ Added ExternalLink icon

function Page() {
  const { jobs, likeJob, applyToJob } = useJobsContext();
  const { userProfile, isAuthenticated } = useGlobalContext();
  const params = useParams();
  const router = useRouter();
  const { id } = params;

  const job = jobs.find((job: Job) => job._id === id);
  const otherJobs = jobs.filter((job: Job) => job._id !== id);

  if (!job) return null;

  const likes = Array.isArray(job.likes) ? job.likes : [];
  const applicants = Array.isArray(job.applicants) ? job.applicants : [];
  const isLiked = !!userProfile?._id && likes.includes(userProfile._id);
  const isApplied = !!userProfile?._id && applicants.includes(userProfile._id);

  const {
    title,
    location,
    description,
    salary,
    createdBy,
    jobType,
    createdAt,
    salaryType,
    negotiable,
    employer_logo, // ✅ Added for Company Logo
    externalLink,  // ✅ Added for External Redirect
    source         // ✅ Added to check source
  } = job;

  // ✅ Fallback logic: Use Company Logo first, then User Profile, then default
  const recruiterName = createdBy?.name || "Prashikshan Recruiter";
  const recruiterAvatar = employer_logo || createdBy?.profilePicture || "/user.png";

  const handleLike = (jobId: string) => {
    likeJob(jobId);
  };

  const handleShare = () => {
  const shareUrl = job.externalLink || window.location.href;
  navigator.clipboard.writeText(shareUrl);
  toast.success("Job link copied to clipboard!");
};

  const formatDescription = (text: string) => {
    if (!text) return "No description provided.";
    if (text.includes("<") && text.includes(">")) return text;

    return text
      .split("\n\n")
      .map((paragraph) => {
        const trimmed = paragraph.trim();
        if (!trimmed) return "";
        if (trimmed.startsWith("#")) {
          const level = trimmed.match(/^#+/)?.[0].length || 1;
          const content = trimmed.replace(/^#+\s*/, "");
          return `<h${Math.min(level, 6)}>${content}</h${Math.min(level, 6)}>`;
        }
        if (trimmed.startsWith("-") || trimmed.startsWith("*") || /^\d+\./.test(trimmed)) {
          const items = trimmed
            .split(/\n(?=[-*\d])/)
            .map((item) => {
              const content = item.replace(/^[-*\d.]+\s*/, "").trim();
              return `<li>${content}</li>`;
            });
          return `<ul>${items.join("")}</ul>`;
        }
        return `<p>${trimmed}</p>`;
      })
      .filter(Boolean)
      .join("");
  };

  // ✅ Handle Application Redirect Logic
  const handleApply = () => {
    // If it's an API job with an external link, redirect to the company site
    if (externalLink && job.source !== "Manual") {
      window.open(externalLink, "_blank", "noopener,noreferrer");
      return;
    }

    // Normal internal application logic
    if (isAuthenticated) {
      if (!isApplied) {
        applyToJob(job._id);
      } else {
        toast.error("You have already applied to this job");
      }
    } else {
      router.push("https://prashikshan.onrender.com/login");
    }
  };

  return (
    <main className="bg-gray-50 min-h-screen">
      <Header />

      <div className="p-4 md:p-6 lg:p-8 mb-8 mx-auto w-full max-w-[1400px] flex flex-col lg:flex-row gap-6 lg:gap-8">
        {/* LEFT SIDEBAR - Position Suggestions */}
        <div className="hidden lg:flex lg:w-[30%] flex-col gap-6">
          <h3 className="font-bold text-lg text-gray-700 ml-1">Active Position</h3>
          <JobCard activeJob job={job} />

          <h3 className="font-bold text-lg text-gray-700 ml-1 mt-4">Other Openings</h3>
          {otherJobs.slice(0, 3).map((otherJob: Job) => (
            <JobCard job={otherJob} key={otherJob._id} />
          ))}
        </div>

        {/* MAIN CONTENT AREA */}
        <div className="flex-1 flex flex-col gap-6">
          <div className="bg-white p-6 md:p-8 rounded-xl shadow-sm border border-gray-100">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 mb-6">
              <div className="flex items-center gap-4">
                <div className="w-14 h-14 relative overflow-hidden rounded-lg flex items-center justify-center bg-gray-100 shrink-0 border border-gray-200">
                  <Image
                    src={recruiterAvatar}
                    alt={recruiterName}
                    width={56}
                    height={56}
                    className="object-contain bg-white"
                  />
                </div>
                <div>
                  <p className="font-bold text-gray-900">{recruiterName}</p>
                  <p className="text-xs text-gray-500 uppercase tracking-wider font-semibold">
                    {source !== "Manual" ? "Direct via " + source : "Verified Recruiter"}
                  </p>
                </div>
              </div>
              <button
                className={`p-2 rounded-full transition-all ${
                  isLiked ? "bg-[#7263f3]/10 text-[#7263f3]" : "bg-gray-100 text-gray-400 hover:bg-gray-200"
                }`}
                onClick={() => {
                  isAuthenticated ? handleLike(job._id) : router.push("https://prashikshan.onrender.com/login");
                }}
              >
                <Bookmark size={24} fill={isLiked ? "currentColor" : "none"} />
              </button>
            </div>

            <h1 className="text-2xl md:text-4xl font-bold text-gray-900 mb-2">{title}</h1>
            <p className="text-gray-500 mb-8 flex items-center gap-2">
              <span className="inline-block w-2 h-2 bg-green-500 rounded-full"></span>
              {location}
            </p>

            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-10">
              <div className="p-4 flex flex-col items-center bg-green-50 border border-green-100 rounded-2xl text-center">
                <span className="text-xs text-green-700 font-medium mb-1">Salary</span>
                <span className="font-bold text-green-900">{formatMoney(salary, "INR")}</span>
                <span className="text-[10px] text-green-600">
                  /{salaryType === "Yearly" ? "pa" : "pcm"}
                </span>
              </div>

              <div className="p-4 flex flex-col items-center bg-purple-50 border border-purple-100 rounded-2xl">
                <span className="text-xs text-purple-700 font-medium mb-1 text-center">Posted</span>
                <span className="font-bold text-purple-900 text-center text-xs">
                  {formatDates(createdAt)}
                </span>
              </div>

              <div className="p-4 flex flex-col items-center bg-blue-50 border border-blue-100 rounded-2xl">
                <span className="text-xs text-blue-700 font-medium mb-1">Applicants</span>
                <span className="font-bold text-blue-900">{applicants.length}</span>
              </div>

              <div className="p-4 flex flex-col items-center bg-yellow-50 border border-yellow-100 rounded-2xl">
                <span className="text-xs text-yellow-700 font-medium mb-1">Type</span>
                <span className="font-bold text-yellow-900 text-xs text-center">
                  {jobType[0] || "Full Time"}
                </span>
              </div>
            </div>

            <h2 className="font-bold text-2xl text-gray-800 mb-4 border-b pb-2">Job Description</h2>
            <div
              className="prose prose-indigo max-w-none text-gray-600 leading-relaxed"
              dangerouslySetInnerHTML={{ __html: formatDescription(description) }}
            ></div>
          </div>
        </div>

        {/* RIGHT SIDEBAR - Actions & New Sections */}
        <div className="w-full lg:w-[26%] flex flex-col gap-6">
          <button
            className={`w-full py-4 rounded-xl font-bold text-lg shadow-lg transition-all transform active:scale-95 flex items-center justify-center gap-2 ${
              isApplied ? "bg-green-500 text-white cursor-default" : "bg-[#7263f3] text-white hover:bg-[#5e4ee0]"
            }`}
            onClick={handleApply}
          >
            {source !== "Manual" ? (
              <>
                Apply on Company Site <ExternalLink size={18} />
              </>
            ) : isApplied ? (
              "Applied"
            ) : (
              "Apply Now"
            )}
          </button>
          {/* ✅ NEW: Share Button */}
          <button 
           onClick={handleShare}
           className="w-full py-3 rounded-xl border-2 border-[#7263f3] text-[#7263f3] font-bold hover:bg-[#7263f3]/5 transition-all flex items-center justify-center gap-2"
           >
            Share this Job
          </button>

          <div className="p-6 bg-white rounded-xl shadow-sm border border-gray-100">
            <h3 className="font-bold text-gray-800 mb-4">Other Information</h3>
            <div className="space-y-3 text-sm">
              <p className="flex justify-between">
                <span className="text-gray-500">Negotiable:</span>{" "}
                <span className={negotiable ? "text-green-600 font-bold" : "text-red-500"}>
                  {negotiable ? "Yes" : "No"}
                </span>
              </p>
              <p className="flex justify-between">
                <span className="text-gray-500">Location:</span>{" "}
                <span className="font-medium text-gray-800">{location}</span>
              </p>
              <p className="flex justify-between">
                <span className="text-gray-500">Job Type:</span>{" "}
                <span className="font-medium text-gray-800">{jobType[0] || "N/A"}</span>
              </p>
            </div>
          </div>

          {/* Tags Section */}
          <div className="p-6 flex flex-col gap-3 bg-white rounded-xl shadow-sm border border-gray-100">
            <h3 className="text-lg font-semibold text-gray-800">Tags</h3>
            <div className="flex flex-wrap gap-2">
              {job.tags && job.tags.length > 0 ? (
                job.tags.map((tag: string, index: number) => (
                  <span
                    key={index}
                    className="px-3 py-1 rounded-full text-xs font-medium bg-red-100 text-red-600 border border-red-200"
                  >
                    {tag}
                  </span>
                ))
              ) : (
                <p className="text-sm text-gray-400">No tags available.</p>
              )}
            </div>
          </div>

          {/* Skills Section */}
          <div className="p-6 flex flex-col gap-3 bg-white rounded-xl shadow-sm border border-gray-100">
            <h3 className="text-lg font-semibold text-gray-800">Skills Required</h3>
            <div className="flex flex-wrap gap-2">
              {job.skills && job.skills.length > 0 ? (
                job.skills.map((skill: string, index: number) => (
                  <span
                    key={index}
                    className="px-3 py-1 rounded-full text-xs font-medium bg-blue-100 text-[#7263f3] border border-blue-200"
                  >
                    {skill}
                  </span>
                ))
              ) : (
                <p className="text-sm text-gray-400">Contact recruiter for skills.</p>
              )}
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </main>
  );
}

export default Page;